type WordmarkProps = {
  className?: string;
};

export function Wordmark({ className }: WordmarkProps) {
  return (
    <span className={className} aria-label="KoreVitta">
      <span style={{ color: "var(--brand-kore)" }}>Kore</span>
      <span style={{ color: "var(--brand-vitta)" }}>Vitta</span>
    </span>
  );
}
